import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ReseniasService } from '../../services/resenias.service';

@Component({
  selector: 'app-detail-cdt',
  templateUrl: './detail-cdt.component.html',
  styleUrls: ['./detail-cdt.component.css']
})
export class DetailCDTComponent implements OnInit {

  filtersLoaded: Promise<boolean>;
  idProd: number;
  private sub: any;
  proceso: any;

  constructor(private reseniasService: ReseniasService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      //this.idProd = +params['id']; // esto es lo que se debe hacer cuando tengamos id para prods
      this.idProd = +1;
      if(this.idProd != null && this.idProd != undefined){
        this.cargarReseniasProducto(this.idProd);
      }
    });

  }

  cargarReseniasProducto(idProd) {
    this.reseniasService.cargarResenias(idProd).subscribe(res => {
      console.log(res);
      this.reseniasService.actualizarResenias(res);
    },
      err => {
        console.log(err);
        return false;
      });
  }

}
