import {ListadoAdquiereComponent} from './components/listado-adquiere/listado-adquiere.component';
import {PreguntasAhorroComponent} from './components/preguntas-ahorro/preguntas-ahorro.component';
import {PreguntasAdquiereComponent} from './components/preguntas-adquiere/preguntas-adquiere.component';
import {RegistroComponent} from './components/registro/registro.component';
import {DashboardComponent} from './components/dashboard/dashboard.component';
import {EmpezarComponent} from './components/empezar/empezar.component';
import {PreguntasCdtComponent} from './components/preguntas-cdt/preguntas-cdt.component';
