import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { interval } from 'rxjs';
import { faCoffee } from '@fortawesome/free-solid-svg-icons';
import { AuthService } from '../../services/auth.service';
import { NgFlashMessageService } from 'ng-flash-messages';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: [
    './home.component.css'
  ]
})
export class HomeComponent implements OnInit {

  correcto = false;
  nombre: String;
  email: String;
  telefono: number;
  acepta: boolean;

  tam = 0;
  faCoffee = faCoffee;

  video = {
    width: 0,
    height: 0
  }

  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessage: NgFlashMessageService) { }




  showNavigationArrows = false;
  showNavigationIndicators = false;

  public innerWidth: any;

  cambiarTamanioVideo = function () {
    this.video.width = (this.innerWidth / 100) * 80;
    this.video.height = (this.video.width / 16) * 9;
  }


  ngOnInit() {
    this.innerWidth = window.innerWidth;
    this.cambiarTamanioVideo();
    // Create an Observable that will publish a value on an interval
    //const secondsCounter = interval(1000);
    // Subscribe to begin publishing values
    //secondsCounter.subscribe(n => this.cargarImagenes());

  }

  onRegistroSubmit() {
    const usuario = {
      nombre: this.nombre,
      email: this.email,
      telefono: this.telefono,
      acepta: this.acepta
    }
    this.authService.registerUser(usuario).subscribe(data => {
      if (data.nombre != undefined) {
        this.correcto = true;
      } else {

      }
    });




  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
    this.cambiarTamanioVideo();
  }

}

